<template><section><html><head></head><body><h1>ArcGisMapServerImageryProvider</h1>
<p><code v-pre="">vc-provider-imagery-arcgis-mapserver</code> 组件用于加载 ArcGIS MapServer 影像服务图层。</p>
<h2>示例</h2>
<h3>加载 ArcGIS MapServer 影像服务图层</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
       <vc-layer-imagery ref="layer" :alpha="alpha" :brightness="brightness" :contrast="contrast">
        <vc-provider-imagery-arcgis-mapserver ref="arcgis" :url="url" :maximum-level="maximumLevel"></vc-provider-imagery-arcgis-mapserver>
       </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>透明度</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>亮度</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>对比度</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>切换服务</span>
        <md-select v-model="url" placeholder="请选择服务">
          <md-option v-for="item in options" :key="item.value" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
</doc-preview>
