<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>ArcGIS 影像服务地址。</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>ArcGIS 影像服务认证 Token。</td>
</tr>
<tr>
<td>tileDiscardPolicy</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The policy that determines if a tile is invalid and should be discarded.</td>
</tr>
<tr>
<td>usePreCachedTilesIfAvailable</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>If true, the server's pre-cached tiles are used if they are available. If false, any pre-cached tiles are ignored and the 'export' service is used.</td>
</tr>
<tr>
<td>layers</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> A comma-separated list of the layers to show, or undefined if all layers should be shown.</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>是否拾取对象，在 infobox 弹出信息。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The tiling scheme to use to divide the world into tiles. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>参考椭球体</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code>像元宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code>像元高度。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>最大层级。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ArcGisMapServerImageryProvider.html">ArcGisMapServerImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
